.btn {
  text-align: center;
  color: #131313;
  background-color: #f8d24e;
  border: rgba(0, 0, 0, 0);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 12px 34px;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.29;
  display: flex;
}

.btn:hover, .btn:focus {
  background-color: #0a8817;
}

.btn:focus {
  color: #fcfcfc;
}

