}

.btn--transparent {
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #f8d24e;
  padding: 12px 46px;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
}

.btn--transparent:hover, .btn--transparent:focus {
  background-color: rgba(0, 0, 0, 0);
  border-color: #0a8817;
}

.btn--transparent:focus {
  color: #fcfcfc;
  border: 2px solid #0a8817;
  outline: none;
}

.btn--small {
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #f8d24e;
  padding: 11px 26px;
  font-size: 12px;
  line-height: 1.25;
}

.btn--small:hover, .btn--small:focus {
  background-color: rgba(0, 0, 0, 0);
  border-color: #0a8817;
}

.btn--small:focus {
  color: #fcfcfc;
