  outline: none;
}

@media screen and (min-width: 1200px) {
  .btn--small {
    padding: 12px 34px;
    font-size: 14px;
  }
}

.btn__icon {
  margin-left: 6px;
}

.btn:focus .btn__icon, .btn--transparent:focus .btn__icon {
  fill: currentColor;
}

.btn-anchor {
  color: #f8d24e;
  text-align: center;
  border: none;
  padding: 11px 26px;
  font-size: 12px;
  font-weight: 600;
